<template>
  <SlideXLeftTransition>
    <div class="side-bar" @click.self="closeSideBar" v-show="show">
      <div class="side-bar__content">
        <div class="side-bar__header">
          <svg class="side-bar__logo">
            <use href="@/assets/img/sprite.svg#logo-small"></use>
          </svg>
          <button class="side-bar__close" @click="closeSideBar">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="32"
              height="32"
              fill="none"
              viewBox="0 0 32 32"
            >
              <path
                stroke="#fff"
                stroke-linecap="round"
                stroke-linejoin="round"
                stroke-width="2"
                d="M22.667 9.333L9.333 22.667m13.334 0L9.333 9.333l13.334 13.334z"
              />
            </svg>
          </button>
        </div>
        <user-drop-down class="side-bar__user" />
        <nav-side-bar class="side-bar__nav" />
        <ul class="side-bar__group-icons">
          <li>
            <a href="#">
              <svg>
                <use href="@/assets/img/sprite.svg#vk"></use>
              </svg>
            </a>
          </li>
          <li>
            <a href="#">
              <svg>
                <use href="@/assets/img/sprite.svg#facebook"></use>
              </svg>
            </a>
          </li>
          <li>
            <a href="#">
              <svg>
                <use href="@/assets/img/sprite.svg#instagram"></use>
              </svg>
            </a>
          </li>
          <li>
            <a href="#">
              <svg>
                <use href="@/assets/img/sprite.svg#youtube"></use>
              </svg>
            </a>
          </li>
        </ul>
      </div>
    </div>
  </SlideXLeftTransition>
</template>

<script>
import { SlideXLeftTransition } from "vue2-transitions";
import UserDropDown from "../UserDropDown";
import NavSideBar from "./NavSideBar";

export default {
  name: "SideBar",
  components: {
    NavSideBar,
    UserDropDown,
    SlideXLeftTransition
  },
  props: {
    show: Boolean
  },
  methods: {
    closeSideBar() {
      this.$emit("update:show", false);
      this.$emit("close");
    }
  }
};
</script>

