<template>
  <div class="main-layout">
    <side-bar :show.sync="showSideBar"></side-bar>
    <div class="main-layout__header">
      <navbar @click="showSideBar = true"></navbar>
    </div>
    <div class="main-layout__content">
      <router-view></router-view>
    </div>
    <div class="main-layout__footer">
      <v-footer></v-footer>
    </div>
  </div>
</template>

<script>
import Navbar from "../../components/Navbar";
import VFooter from "../../components/Footer";
import SideBar from "../../components/Sidebar/SideBar";

export default {
  name: "MainLayout",
  components: { SideBar, Navbar, VFooter },
  data() {
    return {
      showSideBar: false
    };
  },
  computed: {
    isAuthenticated() {
      return this.$store.getters["auth/isAuthenticated"];
    },
    isNotificationsEnabled() {
      return this.$store.getters["auth/isNotificationsEnabled"];
    }
  },
  beforeMount() {
    console.log("isAuthenticated:", this.isAuthenticated);
    console.log("isNotificationsEnabled:", this.isNotificationsEnabled);

    if (this.isAuthenticated && !this.isNotificationsEnabled) {
      this.initOneSignal();
    }
  },
  methods: {
    async initOneSignal() {
      this.$OneSignal
        .init({ appId: process.env.VUE_APP_ONE_SIGNAL })
        .then(() => {
          console.log("init OneSignal");
          this.$OneSignal.isPushNotificationsEnabled(async isSubscribed => {
            console.log("The user's subscription state is now:", isSubscribed);
            if (isSubscribed) {
              const userId = await this.$OneSignal.getUserId();
              await this.$store.dispatch(
                "auth/CREATE_ONE_SIGNAL_TOKEN",
                userId
              );
              console.log("OneSignal", userId);
            } else {
              console.log("OneSignal isDisabled");
            }
          });
        });
    }
  }
};
</script>

<style scoped></style>
