<template>
  <div class="user-drop-down" v-if="isAuthenticated">
    <drop-down>
      <button href="#" class="user-info">
        <div class="user-info__photo">
          <img
            v-if="getUrlAvatar"
            :src="getUrlAvatar"
            alt="user"
            width="40"
            height="40"
          />
          <svg class="navbar__logo" v-if="!getUrlAvatar">
            <use href="@/assets/img/sprite.svg#icon-anonymous-avatar"></use>
          </svg>
          <span
            class="user-info__new-notify"
            v-show="getUser.unread_notifications_count > 0"
          ></span>
        </div>
        <div class="user-info__block">
          <div class="user-info__name">
            {{ getUser.full_name || "анонимный" }}
          </div>
          <div class="user-info__status">{{ getRole }}</div>
        </div>

        <div class="user-info__icon">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="8"
            height="5"
            fill="none"
            viewBox="0 0 8 5"
          >
            <path
              fill="#B2B7BE"
              d="M6.862.195L4 3.057 1.138.195l-.943.943L4 4.943l3.805-3.805-.943-.943z"
            />
          </svg>
        </div>
      </button>

      <template v-slot:menu>
        <ul class="dropdown__menu">
          <li>
            <router-link to="/notification">
              Уведомления
              <span
                class="badge badge--error"
                v-show="getUser.unread_notifications_count > 0"
              >
                {{ getUser.unread_notifications_count }}</span
              >
            </router-link>
          </li>
          <li>
            <router-link :to="'/favorites'"
              >Избранное
              <span
                class="badge badge--warning"
                v-show="getUser.favorite_count > 0"
              >
                {{ getUser.favorite_count }}
              </span>
            </router-link>
          </li>
          <li>
            <router-link to="/tickets"
              >Билеты <span class="badge badge--info">0</span></router-link
            >
          </li>
          <li>
            <router-link to="/user-profile">
              Настройки профиля
            </router-link>
          </li>
          <li><a @click.prevent="Logout">Выйти</a></li>
        </ul>
      </template>
    </drop-down>
  </div>
</template>

<script>
import DropDown from "./Dropdown";
import { mapGetters } from "vuex";

export default {
  name: "UserDropDown",
  components: { DropDown },
  methods: {
    async Logout() {
      await this.$store.dispatch("auth/DELETE_ONE_SIGNAL_TOKEN");
      await this.$store.dispatch("auth/AUTH_LOGOUT");
      this.$router.push("/login");
    }
  },
  computed: {
    ...mapGetters("user", ["getUser", "getRole"]),
    ...mapGetters("auth", ["isAuthenticated"]),
    getUrlAvatar() {
      return this.getUser.avatar ? this.getUser.avatar.url : "";
    }
  }
};
</script>

<style scoped></style>
