<template>
  <div class="navbar">
    <div class="navbar__wrap">
      <button class="navbar__burger mr-15" @click="$emit('click')">
        <span></span>
        <span></span>
        <span></span>
      </button>
      <router-link to="/" class="navbar__logo">
        <svg>
          <use href="@/assets/img/sprite.svg#logo-small"></use>
        </svg>
      </router-link>
      <input
        type="search"
        class="input-field-search navbar__search"
        placeholder="События, арт-стартапы, резиденты"
      />
    </div>
    <button
      class="navbar__btn-login button-warning"
      v-if="!isAuthenticated"
      @click="login"
    >
      Войти
    </button>
    <user-drop-down class="navbar__user side-bar__user user-drop-down--light" />
  </div>
</template>

<script>
import UserDropDown from "./UserDropDown";
import { mapGetters } from "vuex";

export default {
  name: "Navbar",
  components: { UserDropDown },
  data() {
    return {
      timer: null
    };
  },
  computed: {
    ...mapGetters("auth", ["isAuthenticated"])
  },
  methods: {
    login() {
      this.$router.push("/login");
    }
  },
  mounted() {
    if (this.isAuthenticated) {
      this.timer = setInterval(() => {
        this.$store.dispatch("user/BASE_INFO");
      }, 10000);
    }
  },
  beforeDestroy() {
    clearInterval(this.timer);
  }
};
</script>

<style scoped></style>
