<template>
  <component
    :is="tag"
    class="dropdown nav-item"
    :class="{ show: isOpen }"
    @click.stop="toggleDropDown"
    v-click-outside="closeDropDown"
  >
    <slot></slot>
    <div class="dropdown__wrap">
      <slot name="menu"></slot>
    </div>
  </component>
</template>
<script>
import ClickOutside from "vue-click-outside";

export default {
  name: "drop-down",
  props: {
    tag: {
      type: String,
      default: "div"
    }
  },
  data() {
    return {
      isOpen: false
    };
  },
  methods: {
    toggleDropDown() {
      this.isOpen = !this.isOpen;
    },
    closeDropDown() {
      this.isOpen = false;
    }
  },

  directives: {
    ClickOutside
  }
};
</script>

<style></style>
